body {
  margin: 0;
  font-family: 'Raleway', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #282c34;
}

.container {
  width: 39%;
}

.logo-container {
  margin: 7% 0;
}

.logo {
  height:50px;
  margin:0px 15px 0 0;
}

.link {
  color: #282c34;
  text-decoration: underline;
}
.link:visited {
  color: #282c34;
  text-decoration: underline;
}

.btn-custom {
  color: #fff;
  font-weight: bold;
  background: rgb(209, 46, 38);
}
.btn-custom:hover {
  color: #f2f2f2;
}

.privacy-policy-logo {
  float:left; 
  width:80px; 
  height:80px; 
  margin:0px 15px;
}